import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/home',
      name:"Home",
      component: resolve => require.ensure([], () => resolve(require('@/views/Home.vue')), 'home'),
    },
    {
      path: '/noteDetail',
      name:"NoteDetail",
      component: resolve => require.ensure([], () => resolve(require('@/views/NoteDetail.vue')), 'NoteDetail'),
    },
    {
      path: '/aboutMe',
      name: 'AboutMe',
      component: resolve => require.ensure([], () => resolve(require('@/views/About.vue')), 'About')   
    },
    {
      path: '*',
      redirect: '/home'
    }
  ]
})



